<template>
  <div>
    <!-- 面包屑区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图 -->
    <el-card>
      <!-- 添加角色按钮区域 -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="addRoleVisible = true">添加角色</el-button>
        </el-col>
      </el-row>

      <!-- 角色列表区 -->
      <el-table :data="rolesList" border stripe>
        <!-- 展开列 -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              :class="['bdbottom', index1 == 0 ? 'bdtop' : '','vcenter']"
              v-for="(item1,index1) in scope.row.children"
              :key="item1.id"
            >
              <!-- 渲染一级权限 -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row,item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- 渲染二级和三级权限 -->
              <el-col :span="19">
                <!-- 通过循环来渲染二级权限 -->
                <el-row
                  :class="[index2 == 0 ? '' : 'bdtop','vcenter']"
                  v-for="(item2,index2) in item1.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      closable
                      @close="removeRightById(scope.row,item2.id)"
                      type="success"
                    >{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      v-for="(item3) in item2.children"
                      :key="item3.id"
                      closable
                      @close="removeRightById(scope.row,item3.id)"
                      type="warning"
                    >{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!-- 索引列 -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="角色" prop="roleName"></el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
        <el-table-column label="操作" width="300px">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showEditDialog(scope.row)"
            >编辑</el-button>
            <el-button
              @click="removeRoleById(scope.row.id)"
              size="mini"
              type="danger"
              icon="el-icon-delete"
            >删除</el-button>
            <el-button
              @click="showSetRightDialog(scope.row)"
              size="mini"
              type="warning"
              icon="el-icon-setting"
            >分配权限</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!-- 添加用户的对话框 -->
    <el-dialog title="添加用户" :visible.sync="addRoleVisible" width="50%" @close="addDialogClose">
      <!-- 内容主题区 -->
      <el-form :model="addRoleForm" :rules="addRoleFormRules" ref="addFormRef" label-width="80px">
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="addRoleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="addRoleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部区域 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addRoleVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUser">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 修改角色的对话框 -->
    <el-dialog title="修改角色" @close="editDialogClosed" :visible.sync="editRolevisible" width="50%">
      <el-form :model="roleForm" :rules="roleFormRules" ref="editFormRef" label-width="80px">
        <el-form-item label="角色描述" prop="roleName">
          <el-input v-model="roleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="roleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editRolevisible = false">取 消</el-button>
        <el-button type="primary" @click="editUserInfo">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 分配权限的对话框 -->
    <el-dialog @close="setRightDialogClosed" title="分配权限" :visible.sync="setRightDialogVisible" width="50%">
      <!-- 树形控件 -->
      <el-tree ref="treeRef" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" :data="rightsList" :props="treeProps"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="allotRights">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 所有角色列表数据
      rolesList: [],
      // 控制添加角色对话框的显示与隐藏
      addRoleVisible: false,
      // 添加角色的表单数据
      addRoleForm: {
        roleName: '',
        roleDesc: '',
      },
      // 添加表单的验证规则对象
      addRoleFormRules: {
        roleName: [
          { required: true, message: '请输入角色名称', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '角色名称的长度在3-10位之间',
            trigger: 'blur',
          },
        ],
        roleDesc: [
          { required: true, message: '请输入角色描述', trigger: 'blur' },
          {
            min: 6,
            max: 15,
            message: '角色描述的长度在6-15位之间',
            trigger: 'blur',
          },
        ],
      },
      // 控制修改角色对话框的显示与隐藏
      editRolevisible: false,
      // 查询到的角色信息对象
      roleForm: {},
      // 修改表单的验证对象
      roleFormRules: {
        roleName: [
          { required: true, message: '请输入角色名称', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '角色名称的长度在3-10位之间',
            trigger: 'blur',
          },
        ],
        roleDesc: [
          { required: true, message: '请输入角色描述', trigger: 'blur' },
          {
            min: 6,
            max: 15,
            message: '角色描述的长度在6-15位之间',
            trigger: 'blur',
          },
        ],
      },
      // 控制分配权限的对话框的显示与隐藏
      setRightDialogVisible:false,
      // 所有权限的数据
      rightsList:[],
      // 树形控件的绑定对象
      treeProps:{
        label:'authName',
        children:'children'
      },
      // 默认选中的节点ID值节点
      defKeys:[],
      // 当前即将分配权限的角色ID
      roleId:''
    }
  },
  created() {
    this.getRoulesList()
  },
  methods: {
    async getRoulesList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) return this.$message.error('获取数据失败')

      this.rolesList = res.data
    },
    addDialogClose() {
      this.$refs.addFormRef.resetFields()
    },
    // 点击确定按钮 添加用户
    addUser() {
      this.$refs.addFormRef.validate(async (valid) => {
        // console.log(valid)
        if (!valid) return
        // 可以发起添加用户的网络请求
        const { data: res } = await this.$http.post('roles', this.addRoleForm)
        // console.log(res)
        if (res.meta.status !== 201) {
          this.$message.error('添加角色失败')
        }
        this.$message.success('添加角色成功')
        // 隐藏添加用户的对话框
        this.addRoleVisible = false

        // 重新获取用户列表数据
        this.getRoulesList()
      })
    },
    // 展示编辑用户的对话框
    async showEditDialog(id) {
      const { data: res } = await this.$http.put(`roles/${id.id}`, {
        roleName: id.roleName,
        roleDesc: id.roleDesc,
      })
      // console.log(res)
      if (res.meta.status !== 200)
        return this.$message.error('查询用户信息失败')
      this.roleForm = res.data
      this.editRolevisible = true
    },
    // 监听修改用户对话框的关闭事件
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // 修改用户信息并提交
    editUserInfo() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return
        const { data: res } = await this.$http.put(
          'roles/' + this.roleForm.roleId,
          { roleName: this.roleForm.roleName, roleDesc: this.roleForm.roleDesc }
        )
        // console.log(res)
        if (res.meta.status !== 200) return this.$message.error('修改不通过')
        // 对话框关闭
        this.editRolevisible = false
        // 刷新数据列表
        this.getRoulesList()
        // 提示显示成功
        this.$message.success('修改成功')
      })
    },
    // 根据id删除对应的用户信息
    async removeRoleById(id) {
      // 询问用户是否删除数据
      const conFirmResult = await this.$confirm(
        '此操作将永久删除该用户, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).catch((err) => {
        return err
      })
      // 如果用户删除则返回值为字符串 confirm
      // 如果用户取消删除则返回值为字符换 cancel
      // console.log(conFirmResult)
      if (conFirmResult !== 'confirm') {
        return this.$message.info('已经取消删除')
      }
      const { data: res } = await this.$http.delete(`roles/${id}`)
      // console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('删除用户失败')
      }
      this.$message.success('删除用户成功')
      this.getRoulesList()
    },
    // 根据对应的ID删除对应的权限
    async removeRightById(role, rightId) {
      // 弹框提示是否删除角色
      const conFirmResult = await this.$confirm(
        '此操作将永久删除该文件, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).catch((err) => err)

      if (conFirmResult !== 'confirm') {
        return this.$message.error('取消了删除')
      }

      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      )

      if (res.meta.status !== 200) return this.$message.error('删除权限失败')
      // this.getRoulesList()
      role.children = res.data
    },
    // 展示分配权限的对话框
    async showSetRightDialog(role) {
        // 角色的ID
        this.roleId = role.id
        // 获取所有权限的数据
        const { data: res} = await this.$http.get('rights/tree')
        if(res.meta.status !== 200) return this.$message.error('获取权限失败')
        
        // 获取到的权限数据保存到data中
        this.rightsList = res.data


        //递归获取三级节点的ID  
        this.getleafKeys(role,this.defKeys)

        // 对话框显示
        this.setRightDialogVisible = true
    },
    // 通过递归的形式获取角色下所有三级权限的ID并保存到数组defKeys中
    getleafKeys(node,arr){
        // 如果当前nonde节点不包含children属性则是三级节点
        if(!node.children){
            return arr.push(node.id)
        }

        node.children.forEach((item)=>{
            this.getleafKeys(item,arr)
        })
    },
    // 监听分配权限对话框的关闭事件
    setRightDialogClosed(){
        this.defKeys = []
    },
    //分类权限对话框的确定授权事件
    async allotRights(){
        const keys = [
            // 所有已选中的权限
            ...this.$refs.treeRef.getCheckedKeys(),
            // 所有半选中的权限
            ...this.$refs.treeRef.getHalfCheckedKeys()
        ]
        const idstr = keys.join(',')
        const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`,{rids:idstr})
        if(res.meta.status !== 200) return this.$message.error('分配权限失败')
        this.$message.success('分配权限成功')
        this.getRoulesList()
        this.setRightDialogVisible =false
    }
  },
}
</script>

<style scoped>
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>