<template>
  <div>
    <!-- 面包屑区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图区域 -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCate">添加分类</el-button>
        </el-col>
      </el-row>

      <!-- 表格区域 -->
      <tree-table
        :show-row-hover="false"
        border
        index-text="#"
        show-index
        :expand-type="false"
        :selection-type="false"
        :data="cateList"
        :columns="columns"
        class="tree-table"
      >
        <!-- 是否有效 -->
        <template slot="isok" slot-scope="scope">
          <i
            class="el-icon-success"
            v-if="scope.row.cat_deleted === false"
            style="color:lightgreen"
          ></i>
          <i class="el-icon-error" v-else style="color:red"></i>
        </template>
        <!-- 排序 -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level == 0">一级</el-tag>
          <el-tag size="mini" type="success" v-else-if="scope.row.cat_level == 1">二级</el-tag>
          <el-tag size="mini" type="warning" v-else>三级</el-tag>
        </template>
        <!-- 操作 -->
        <template slot="opt" slot-scope="scope">
          <el-button @click="showEditDialog(scope.row.cat_id)" size="mini" type="primary" icon="el-icon-edit">编辑</el-button>
          <el-button @click="removeUserById(scope.row.cat_id)" size="mini" type="danger" icon="el-icon-delete">删除</el-button>
        </template>
      </tree-table>

      <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!-- 添加分类的对话框 -->
    <el-dialog @close="addCateDialogClosed" title="添加分类" :visible.sync="addCateVisible" width="50%">
      <!-- 添加分类的表单 -->
      <el-form
        :model="addCateForm"
        :rules="addCateFormRules"
        ref="addCateFormRef"
        label-width="100px"
      >
        <el-form-item label="分类名称:" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="父级分类:">
          <!-- options用来指定数据源 -->
          <el-cascader
            v-model="selectKeys"
            :options="parentCateList"
            @change="parengCateChange"
            expand-trigger="hover"
            :props="cascaderProps"
            clearable
            :change-on-select="true"
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCate">确 定</el-button>
      </span>
    </el-dialog>


    <!-- 修改用户的对话框 -->
    <el-dialog title="修改用户" @close="editDialogClosed" :visible.sync="editDialogvisible" width="50%">
      <el-form :model="editForm" ref="editFormRef" label-width="80px">
        
        <el-form-item label="分类名称" prop="email">
          <el-input v-model="editForm.cat_name"></el-input>
        </el-form-item>
        
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogvisible = false">取 消</el-button>
        <el-button type="primary" @click="editUserInfo">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 商品分类的数据列表
      cateList: [],
      // 查询条件
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5,
      },
      // 总数据条数
      total: 0,
      // 为table指定列的定义
      columns: [
        {
          label: '分类名称',
          prop: 'cat_name',
        },
        {
          label: '是否有效',
          // 表示当前列为定义模板列
          type: 'template',
          // 表示当前这一列使用模板名称
          template: 'isok',
        },
        {
          label: '排序',
          // 表示当前列为定义模板列
          type: 'template',
          // 表示当前这一列使用模板名称
          template: 'order',
        },
        {
          label: '操作',
          // 表示当前列为定义模板列
          type: 'template',
          // 表示当前这一列使用模板名称
          template: 'opt',
        },
      ],
      // 控制分类对话框的显示与隐藏
      addCateVisible: false,
      // 添加分类的表单数据对象
      addCateForm: {
        //将要添加的分类的名称
        cat_name: '',
        // 父级分类的ID
        cat_pid: 0,
        // 分类等级默认添加的是一级分类
        cat_level: 0,
      },
      // 添加分类表单的验证规则对象
      addCateFormRules: {
        cat_name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' },
        ],
      },
      // 父级分类的列表
      parentCateList: [],
      // 指定级联选择器的配置对象
      cascaderProps:{
        value:'cat_id',
        label:'cat_name',
        children:'children'
      },
      // 选中的父级分类的ID数组
      selectKeys:[],
      // 控制修改用户对话框的显示与隐藏
      editDialogvisible: false,
    // 查询到的用户信息对象
      editForm: {},
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 获取分类数据
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo,
      })

      if (res.meta.status !== 200) return this.$message.error('获取失败')

      this.cateList = res.data.result

      this.total = res.data.total
    },
    // 监听pagesize改变的事件
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getCateList()
    },
    // 监听pagenum的改变
    handleCurrentChange(newpage) {
      this.queryInfo.pagenum = newpage
      this.getCateList()
    },
    // 点击按钮展示添加分类的对话框
    showAddCate() {
      // 先获取父级的分类列表在展示对话框
      this.getparentCateList()
      this.addCateVisible = true
    },
    // 获取父级分类的数据列表
    async getparentCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 },
      })
      if (res.meta.status !== 200)
        return this.$message.error('获取父级分类失败')
      this.parentCateList = res.data
    },
    // 选择项发生变化触发这个函数
    parengCateChange(){
        // console.log(this.selectKeys)
        // 如果 selectKeys 数组中的length 大于 0 证明选中的父级分类
        // 反之就说名没有选中任何父级分类
        if(this.selectKeys.length>0){
            // 父级分类的id
            this.addCateForm.cat_pid = this.selectKeys[this.selectKeys.length-1]
            // 为当前分类的等级赋值
            this.addCateForm.cat_level = this.selectKeys.length
            return
        }else{
            this.addCateForm.cat_pid = 0
            this.addCateForm.cat_level = 0
        }
        
    },
    // 点击按钮添加新的分类
    addCate(){
        this.$refs.addCateFormRef.validate(async (valide)=>{
            if(!valide) return 
            const { data: res } = await this.$http.post('categories', this.addCateForm)
            if(res.meta.status !== 201) return this.$message.error('添加分类失败')
            this.$message.success('添加分类成功')

            this.getCateList()
            this.addCateVisible = false
        })
    },
    // 监听对话框的关闭事件并重置表单
    addCateDialogClosed(){
        this.$refs.addCateFormRef.resetFields()
        this.selectKeys = []
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_level = 0
    },
    // 展示编辑添加分类的对话框
    async showEditDialog(id) {
      const { data: res } = await this.$http.get(`categories/${id}`)
      // console.log(res)
      if (res.meta.status !== 200)
        return this.$message.error('查询分类信息失败')
      this.editForm = res.data
      this.editDialogvisible = true
    },
    // 监听修改用户对话框的关闭事件
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // 修改用户信息并提交
    async editUserInfo() {
      
        
        const { data: res } = await this.$http.put(
          'categories/' + this.editForm.cat_id,
          { cat_name: this.editForm.cat_name }
        )
        if (res.meta.status !== 200) return this.$message.error('修改不通过')
        // 对话框关闭
        this.editDialogvisible = false
        // 刷新数据列表
        this.getCateList()
        // 提示显示成功
        this.$message.success('修改成功')
      
    },
    // 根据id删除对应的用户信息
    async removeUserById(id) {
      // 询问用户是否删除数据
      const conFirmResult = await this.$confirm(
        '此操作将永久删除该用户, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).catch((err) => {
        return err
      })
      // 如果用户删除则返回值为字符串 confirm
      // 如果用户取消删除则返回值为字符换 cancel
      // console.log(conFirmResult)
      if (conFirmResult !== 'confirm') {
        return this.$message.info('已经取消删除')
      }
      const { data: res } = await this.$http.delete(`categories/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('删除用户失败')
      }
      this.$message.success('删除用户成功')
      this.getCateList()
    },
  },
}
</script>

<style scoped>
.tree-table {
  margin-top: 15px;
}
</style>